<template>
  <div class="newField pageSpace h100">
    <my-breadcrumb></my-breadcrumb>
    <div class="pageContent">
      <a-layout class="w100 h100">
        <a-layout-sider width="300" style="overflow-y:auto">
            <div v-if="treeData.length>0">
                <a-tree :tree-data="treeData" :show-line="true" :defaultExpandedKeys="defaultExpandedKeys" :defaultSelectedKeys="defaultSelctedKeys" :replaceFields="replaceFields" @select="nodeClick"></a-tree>
            </div>
            <div v-else>
                <a-alert type="info" message="未发现数据"></a-alert>
            </div>
        </a-layout-sider>
        <a-layout class="in-content"> 
          <field :fieldData="dicData" :currentNode="currentNode" @refresh="getDicData(currentNode.id,currentNode.ownerType)"></field>
        </a-layout>
      </a-layout>
    </div>
  </div>
</template>
<script>
import field from './field'
export default {
  name: "newField",
  components:{
    field
  },
  data(){
      return {
          treeData:[],
          replaceFields:{
              children:"children",
              title:"name",
              key:"id",
          },
          dicData:[],
          currentNode:{},
          defaultSelctedKeys:[],
          defaultExpandedKeys:[]
      }
  },
  computed:{
  },
  watch:{
    
  },
  methods:{
    // 获取树结构数据
      getTreeData(){
          this.$API.searchTreeGlobal().then(res => {
              if(res.code === 1000){
                  this.treeData = this.getParentByChildId(res.data);
                  this.defaultSelctedKeys = [this.treeData[0].id];
                  // 这里展开节点进行了处理，只展开第一层（一共三层）
                  this.defaultExpandedKeys = this.treeData.map(list => list.id);
                  // 由于字段的添加编辑界面处于另一个页面，也就说在添加编辑完成后退回到本页面需要选中添加编辑前的选择字段
                  // 1.如果事件前的节点处于第三级，需要展开二级父节点并选中本节点，所以defaultExpandedKeys添加二级父节点，defaultSelctedKeys添加本节点
                  // 2.如果不处于第三级，只修改defaultSelctedKeys添加本节点即可
                  if(sessionStorage.getItem("curNode")){
                    this.currentNode = JSON.parse(sessionStorage.getItem("curNode"));
                    this.defaultSelctedKeys = [this.currentNode.id];
                    if(this.currentNode.ownerType === 2){
                      this.defaultExpandedKeys = [...this.defaultExpandedKeys,this.currentNode.pid];
                    }
                  } else {
                    this.currentNode = this.treeData[0];
                    sessionStorage.setItem('curNode',JSON.stringify(this.currentNode));
                  }
                  this.getDicData(this.currentNode.id,this.currentNode.ownerType);
              } else {  
                  this.treeData = [];
                  this.$message.warning(res.message);
              }
          })
      },
      // 获取字典数据
      getDicData(id,ownerType){
          this.$API.searchDic({id,ownerType}).then(res => {
              if(res.code === 1000){
                  this.dicData = res.data;
              } else {
                  this.dicData = [];
                  this.$message.warning(res.message);
              }
          })
      },
      // 点击节点触发
      nodeClick(key,e){
        let selectNode = e.selectedNodes[0].data.props;
        this.currentNode = selectNode;
        sessionStorage.setItem('curNode',JSON.stringify(this.currentNode));
        this.getDicData(selectNode.id,selectNode.ownerType);
      },
      // 处理树节点添加父节点(没有做递归，仅三级)
      getParentByChildId(data){
        data.forEach(fNode => {
          fNode.pid = 0;
          fNode.children.forEach(sNode => {
            sNode.pid = fNode.id;
            sNode.children.forEach(tNode => {
              tNode.pid = sNode.id;
            })
          })
        });
        return data;
      }
      
  },
  created(){
      this.getTreeData();
  }
};
</script>
<style>
.newField .ant-layout-sider {
  border-right: 1px solid #5dc2fe;
  padding-right: 10px;
}
.newField .ant-layout-sider,
.newField .ant-layout {
  background: transparent;
}
.newField .ant-tree {
  color: #fff;
}
.newField .ant-tree li .ant-tree-node-content-wrapper {
    color: #d9d9d9;
}
.newField .ant-tree.ant-tree-show-line li span.ant-tree-switcher {
    background: transparent;
    color:#faad14;
    
}
.in-content {
  padding: 10px;
}
</style>